.login-wrap {
  width: 100%;
}

.login-signin-wrapp {
  margin: 20px auto 0;
  width: 650px;
  background-color: #fff;
  padding: 86px 100px 60px;
  box-sizing: border-box;
  border-radius: 30px 0
}

.login-signin-tit> ul {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}
.login-signin-tit> ul> li {
  color: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 26px;
  font-weight: bold;
  line-height: 40px;
  box-sizing: border-box;
}

.login-signin-wrapp p {
  color: #999;
  text-align: center;
  letter-spacing: -0.05em;
  line-height: 26px;
  margin-top: 22px;
}

.login-signin-input {
  margin-top: 46px;
}

 .login-input-set> ul {
  width: 390px
}

.login-input_set> ul> li {
  /* float: left; */
  position: relative;
  font-weight: 300;
  width: 390px;
  line-height: 35px;
  letter-spacing: -0.05em;
}
.login-input-set> ul> li> .login-form-label {
  display: inline-block;
  font-weight: 300;
  width: 120px;
  height: 55px;
  line-height: 55px;
  letter-spacing: -0.05em;
}
.login-input-set> ul> li> .login-form-text {
  display: inline-block;
  font-weight: 300;
  width: 260px;
  height: 55px;
  line-height: 55px;
  letter-spacing: -0.05em;
}
.login-signin-wrapp .login-signin-input .login-form-checkbox {
  font-weight: 300;
  color: #666;
  margin-top: 10px;
}

.login-signin-wrapp .login-signin-btn {
  margin-top: 40px;
}

.login-signin-wrapp .login-signin-btn .btn {
  border-radius: 0;
  width: 100% !important;
}

.login-signin-wrapp .login-signin-btn .btn span {
  height: 70px;
  line-height: 70px;
}

.login-signin-wrapp .login-signin-link {
  margin-top: 30px;
}

.signin-wrlogin-signin-wrappapper .login-signin-link ul {
  padding: 0 18px;
  width: 100%;
  box-sizing: border-box;
  display: table
}

.login-signin-wrapp .login-signin-link li {
  display: table-cell;
  text-align: center;
  position: relative;
}

.login-signin-wrapp .login-signin-link li:after {
  content: '';
  position: absolute;
  top: 2px;
  right: -3px;
  width: 1px;
  height: 16px;
  background-color: #ddd
}

.login-signin-wrapp .login-signin-link li:first-child {
  text-align: left;
}

.login-signin-wrapp .login-signin-link li:first-child:after {
  right: 8px;
}

.login-signin-wrapp .login-signin-link li:last-child {
  text-align: right;
}

.login-signin-wrapp .login-signin-link li:last-child:after {
  display: none;
}

.login-signin-wrapp .login-signin-link li a {
  color: #999;
  font-weight: 300;
  letter-spacing: -0.05em;
}